<template>
    <d2-container>
        <template slot="header">提币订单审核 <i class="el-icon-close detail-close" @click="$router.go(-1)"></i></template>
        <div class="withdraw-service">
            <div class="title">提币订单审核</div>
            <div class="info-box">
                <ul class="info">
                    <li>
                        <span>用户ID：</span>
                        <p>{{dataList.userId}}</p>
                    </li>
                    <li>
                        <span>用户账号：</span>
                        <p>{{dataList.userMobile}}</p>
                    </li>
                    <li>
                        <span>用户昵称：</span>
                        <p>{{dataList.userNickName}}</p>
                    </li>
                    <li>
                        <span>提币地址：</span>
                        <p>{{dataList.coinConstractAddress}}</p>
                    </li>
                </ul>
                <ul class="info info-below">
                    <li>
                        <span>订单编号：</span>
                        <p>{{dataList.id}}</p>
                    </li>
                    <li>
                        <span>订单生成时间：</span>
                        <p>{{dataList.createTime}}</p>
                    </li>
                    <li>
                        <span>提币金额及币种：</span>
                        <p>{{dataList.operateAmount}}</p>
                        <span style="padding-left:60px;">手续费：</span>
                        <p>{{dataList.serviceCharge}}</p>
                    </li>
                    <li>
                        <span>订单状态：</span>
                        <p>{{dataList.status}}</p>
                    </li>
                </ul>
            </div>
            <div class="operate">
                <h3>客服处理</h3>
                <ul class="operate-list">
                    <li class="operate-item">
                        <span class="item-tit">客服工号</span>
                        <span class="item-txt">{{dataList.updateUserId}}</span>
                    </li>
                    <li  class="operate-item">
                        <span class="item-tit">处理结果</span>
                        <el-button-group class="operate-btn">
                            <el-button type="info" plain
                                       v-for="item,index in statusBtn"
                                       :key="index"
                                       :class="{'btnActive':statusBtnActive === index}"
                                       @click.native="changeStatusBtnActive(index)">
                                      {{item}}
                            </el-button>
                        </el-button-group>
                    </li>
                    <li  class="operate-item textarea-box" v-if="statusBtnActive === 1">
                        <div class = "text-box">
                            <span class="item-tit">驳回理由</span>
                            <textarea placeholder="请输入" maxlength="500" @input="descInput" v-model="desc"/>
                            <span class="numberV">{{txtVal}}/500</span>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="result-btn">
                <el-button type="info" plain class="cancel-btn" @click="$router.go(-1)">取消</el-button>
                <el-button type="info" plain class="save-btn" @click="confirm">保存</el-button>
            </div>
        </div>
    </d2-container>
</template>

<script>
    export default {
      name: "service",
      data(){
        return{
          statusBtnActive:1,
          statusBtn:['审核通过','审核未通过'],
          txtVal: 0,
          desc:"",
          recordId:'',
          dataList:[]
        }
      },
      methods:{
        //文本框字数
        descInput() {
            this.txtVal = this.desc.length;
        },
        //弹窗
        confirm() {
            let that = this;
            if (!this.statusBtnActive) {
                this.$confirm('通过', '审核结果确认', {
                    iconClass: '',
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    dangerouslyUseHTMLString: true,
                    center:true
                }).then(() => {
                    this.getServiceResult(this.recordId,1,'');
                    setTimeout(function(){
                      that.$router.replace('/order/withdraw');
                    },1000)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '未保存处理结果'
                    });
                });
            } else if ( this.statusBtnActive === 1 && this.desc !== '') {
                this.$confirm('未通过', '审核结果确认', {
                    iconClass: '',
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    dangerouslyUseHTMLString: true,
                    center:true
                }).then(() => {
                    this.getServiceResult(this.recordId,2,this.desc);
                    setTimeout(function(){
                        that.$router.replace('/order/withdraw');
                    },1000)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '未保存处理结果'
                    });
                });
            } else if (this.statusBtnActive === 1 && this.desc === '') {
                this.$message.error( '请填写驳回理由');
            }
        },
        //切换按钮组
        changeStatusBtnActive(index){
            for(var i=0; i<this.statusBtn.length; i++){
                this.statusBtnActive = index;
            }
        },
        //获取提币订单详情
        getWithdrawService(recordId){
          let vm = this;
          this.axiosGET(vm, vm.$API.API.withdrawDetail,
            'application/x-www-form-urlencoded',
            {recordId},
            vm.getSuccessCB,
            vm.getErrorCB
          )
        },
        //成功回调
        getSuccessCB(vm,res){
          if(!res.data || res.data === ''){
            vm.dataList = [];
          } else {
            vm.setData(res.data);
          }
        },
        //失败回调
        getErrorCB(err){
          console.log(err)
        },
        //设置数据
        setData(_data){
          let list = [];
          if(!_data.userId || _data.userId === ''){  //用户id
            list.userId = '-';
          } else {
            list.userId = _data.userId;
          }
          if(!_data.userMobile || _data.userMobile === ''){  //用户账号
            list.userMobile = '-';
          } else {
            list.userMobile = _data.userMobile;
          }
          if(!_data.userNickName || _data.userNickName === ''){ //用户昵称
            list.userNickName = '-';
          } else {
            list.userNickName = _data.userNickName;
          }
          if(!_data.id || _data.id === ''){ //订单编号
            list.id = '-';
          } else {
            list.id = _data.id;
          }
          if(!_data.createTime || _data.createTime === ''){  //创建时间
            list.createTime = '-';
          } else {
            list.createTime = _data.createTime;
          }
          if(!_data.operateAmount || _data.operateAmount === ''){ //充值金额及币种
            list.operateAmount = '-';
          } else {
            list.operateAmount = _data.operateAmount + _data.coinAbbreviation;
          }
          if(!_data.serviceCharge || _data.serviceCharge === ''){  //手续费
            list.serviceCharge = '-';
          } else {
            list.serviceCharge = _data.serviceCharge;
          }
          if(_data.status === 1){ //订单状态
            list.status = '处理中';
          } else if (_data.status === 2){
            list.status = '异常';
          } else if (_data.status === 3){
            list.status = '成功';
          } else if (_data.status === 4){
            list.status = '失败';
          }if (_data.status === 5){
            list.status = '审核中';
          } else {
            list.status = '审核未通过';
          }
          if(!_data.coinConstractAddress || _data.coinConstractAddress === ''){ //提币地址
            list.coinConstractAddress = '-';
          } else {
            list.coinConstractAddress = _data.coinConstractAddress;
          }
          if(!_data.updateUserId || _data.updateUserId === ''){ //客服工号
            list.updateUserId = '-';
          } else {
            list.updateUserId = _data.updateUserId;
          }
          this.dataList = list;
        },
        //获取客服处理详情
        getServiceResult(recordId,type,comment){
          let vm = this;
          this.axiosPOST(vm, vm.$API.API.withdrawAudit,
            'application/x-www-form-urlencoded',
            {recordId,type,comment},
            vm.getServiceSuccessCB,
            vm.getErrorCB
          )
        },
        //客服处理成回调
        getServiceSuccessCB(){}
      },
      mounted(){
        this.recordId = this.$route.query.recordId;
        this.getWithdrawService(this.recordId)
      }
    }
</script>

<style scoped lang="scss">
    .withdraw-service{
        width:100%;
        text-align:center;
        margin:0 auto 50px;
        .title{
            width:100%;
            margin:30px auto 20px;
            font-size:18px;
            text-align:center;
            font-weight:600;
        }
        .info-box{
            padding:60px 200px 0;
            text-align:left;
            .info{
                width:450px;
                text-align:center;
                padding:0 30px;
                border:1px solid #d9d9d9;
                li{
                    list-style:none;
                    width:100%;
                    height:60px;
                    text-align:left;
                    span{
                        display:inline-block;
                        font-size:14px;
                        font-weight:700;
                        line-height:60px;
                        padding-right:10px;
                        color:#525252;
                    }
                    p{
                        display:inline-block;
                        font-size:14px;
                        line-height:60px;
                        color:#808080;
                    }
                }
            }
            .info-below{
                width:500px;
                border:0;
            }
        }
        .operate{
            margin:0 auto;
            padding:60px 200px 0;
            text-align:left;
            h3{
                width:100%;
                margin:0 auto;
                text-align:center;
                display:inline-block;
                height:70px;
                font-size:15px;
                line-height:70px;
                border-bottom:1px solid #d9d9d9;
            }
            .operate-list{
                width:100%;
            }
        }
        .operate-item{
            width:100%;
            height:60px;
            list-style:none;
            text-align:left;
            .item-tit{
                display: inline-block;
                vertical-align: top;
                font-size: 14px;
                font-weight: 700;
                line-height: 60px;
                padding-right: 10px;
                color: #525252;
            }
            .item-txt{
                display: inline-block;
                vertical-align: top;
                font-size: 14px;
                line-height: 60px;
                color: #808080;
            }
            .operate-btn{
                display:inline-block;
                vertical-align: top;
                margin-top:10px;
            }
        }
        .textarea-box{
            min-height:165px;
            text-align:left;
            .text-box{
                position:relative;
                width:800px;
                .item-tit{
                    display: inline-block;
                    vertical-align: top;
                    font-size: 14px;
                    font-weight: 700;
                    line-height: 60px;
                    padding-right: 10px;
                    color: #525252;
                }
                textarea{
                    display: inline-block;
                    vertical-align: top;
                    width:600px;
                    color:#525252;
                    min-height:120px;
                    margin-top:23px;
                    padding:10px 12px;
                    font-size:14px;
                    border:1px solid #d9d9d9;
                }
                .numberV{
                    position:absolute;
                    right:120px;
                    bottom:15px;
                    color:#9e9e9e;
                }
            }
        }
        .result-btn{
            margin:40px auto 0;
            text-align:center;
        }
        .cancel-btn,.save-btn{
            display:inline-block;
            width:76px;
            height:36px;
            padding:6px 20px;
            span{
                line-height:36px;
                vertical-align:top;
            }
        }
    }
</style>